// client/src/pages/DeliveryLogin.tsx

import { useState } from "react"; // `useState`  `usestate`   
import { useToast } from "@/hooks/use-toast"; // `@/hooks/use-toast`    `hooks`  , `/hooks` 
import { Button } from "@/components/ui/button"; // `Button`  `button`   
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"; // `Card`, `CardContent`, `CardHeader`, `CardTitle`   
import { useNavigate } from "react-router-dom"; // `useNavigate`  `usenavigate`   
import { Truck } from "lucide-react"; // `Truck`  `truck`   
import { signInWithPopup, GoogleAuthProvider } from "firebase/auth"; // `signInWithPopup`, `GoogleAuthProvider`  `signinwithpopup`, `googleauthprovider`   
import { auth } from "@/lib/firebase"; // `auth`  `auth`   , `@/lib`   
import { apiRequest } from "@/lib/queryClient"; // `apiRequest`  `apirequest`   , `@/lib`   

export default function DeliveryLogin() { // `DeliveryLogin`  `deliverylogin`   
  const navigate = useNavigate();
  const { toast } = useToast();
  const [loading, setLoading] = useState(false); // `setLoading`  `setloading`   , `useState`  `usestate`   

  const handleGoogleLogin = async () => { // `handleGoogleLogin`  `handlegooglelogin`   
    setLoading(true);
    try {
      const provider = new GoogleAuthProvider(); // `GoogleAuthProvider`   
      const result = await signInWithPopup(auth, provider); // `signInWithPopup`   

      if (result && result.user) {
        const user = result.user;
        const token = await user.getIdToken(); // `getIdToken`  `getidtoken`   

        //      API call 
        const backendResponse = await apiRequest("POST", "/api/delivery/login", { // `backendResponse`  `backendresponse`   , `POST`  `post`   
          firebaseUid: user.uid, // `firebaseUid`  `firebaseuid`   
          email: user.email || "",
          name: user.displayName || user.email || "", // `displayName`  `displayname`   
        });
        
        const deliveryBoy = { ...backendResponse.user, role: "delivery" }; // `deliveryBoy`  `deliveryboy`   , `backendResponse`   
        
        if (!deliveryBoy) {
          throw new Error("Delivery boy data not received from backend."); // `Error`  `error`   
        }

        // localStorage       ()
        localStorage.setItem("deliveryBoyToken", token); // `localStorage.setItem`  `localstorage.setitem`   , "deliveryBoyToken"   
        localStorage.setItem("deliveryBoyEmail", deliveryBoy.email || ""); // "deliveryBoyEmail"   

        if (deliveryBoy.approvalStatus === "approved") { // `approvalStatus`  `approvalstatus`   
          toast({ title: "Login Successful", description: `Welcome ${deliveryBoy.name || deliveryBoy.email}` }); // `Login Successful`  `Welcome`  `login successful`  `welcome`   
          navigate("/delivery-page");
        } else {
          toast({
            title: "Approval Pending",
            description: "You are not approved yet. Please wait for admin approval.", // `Approval Pending`  `You are not approved yet`  `approval pending`  `you are not approved yet`   
            variant: "destructive",
          });
        }
      }
    } catch (err: any) {
      console.error("Delivery login (popup) failed:", err); // `Delivery login`   
      if (err.code !== 'auth/popup-closed-by-user' && err.code !== 'auth/cancelled-popup-request') {
        toast({
          title: "Login Failed",
          description: err.message || "Something went wrong during login.", // `Login Failed`  `Something went wrong`  `login failed`  `something went wrong`   
          variant: "destructive",
        });
      }
    } finally {
      setLoading(false);
    }
  };

  const handleRegister = () => { // `handleRegister`  `handleregister`   
    navigate("/delivery-apply");
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-50 to-indigo-100 px-4 py-12"> {/* `className`  `classname`    */}
      <div className="max-w-md w-full"> {/* `className`  `classname`    */}
        <Card className="shadow-xl"> {/* `Card`  `className`    */}
          <CardHeader className="text-center"> {/* `CardHeader`  `className`    */}
            <div className="w-16 h-16 mx-auto bg-blue-600 rounded-full flex items-center justify-center mb-4"> {/* `className`  `classname`    */}
              <Truck className="w-8 h-8 text-white" /> {/* `Truck`  `className`    */}
            </div>
            <CardTitle className="text-2xl font-bold">Welcome Delivery Boy</CardTitle> {/* `CardTitle`  `className`    */}
          </CardHeader>
          <CardContent className="space-y-4"> {/* `CardContent`  `className`    */}
            <Button // `Button`   
              className="w-full" // `className`  `classname`   
              onClick={handleGoogleLogin} // `onClick`  `onclick`   , `handleGoogleLogin`   
              disabled={loading}
            >
              {loading ? "Checking..." : "Login with Google"} {/* "Checking..."  "Login with Google"  "checking..."  "login with google"    */}
            </Button>
            <Button // `Button`   
              variant="outline"
              className="w-full" // `className`  `classname`   
              onClick={handleRegister} // `onClick`  `onclick`   , `handleRegister`   
              disabled={loading}
            >
              Register
            </Button>
          </CardContent>
        </Card>
        <p className="mt-6 text-center text-sm text-gray-600"> {/* `className`  `classname`    */}
          Need help? Contact support@shopnish.com {/* "Need help?"    */}
        </p>
      </div>
    </div>
  );
}
